.toppicks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.toppicks__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 90%;
    height: 80%;
}

.picks_title {
    font-size: 2rem;
    font-weight: var(--font-bold);
    color: var(--title-color);
    text-transform: uppercase;
    letter-spacing: 0.525rem;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.toppicks__cards {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.toppicks__card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 0.5rem;
    cursor: pointer;
    margin-left: 3rem;
    position: relative;
}

.toppicks__card img {
    width: 18rem;
    height: 16.5rem;
    object-fit: cover;
    border-radius: 1rem;
    border: 0.5px solid var(--text-color);
}

.toppicks__card h2 {
    font-size: 1.2rem;
    font-weight: var(--font-bold);
    color: var(--title-color);
    text-transform: uppercase;
    letter-spacing: 0.125rem;
}

.card__subtitle {
    font-size: 0.8rem;
    font-weight: var(--font-medium);
    color: var(--text-color);
    letter-spacing: 0.125rem;
}

.card__price {
    font-size: 1.1rem;
    font-weight: var(--font-bold);
    color: var(--accent-color);
    letter-spacing: 0.125rem;
    font-family: "Satoshi-Bold", cursive;
}

.card__bottom {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 85%;
}

.toppicks__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
}


.toppicks__carousel .swiper-slide img {
    width: 22rem;
    height: 18.5rem;
    object-fit: cover;
    border-radius: 1rem;
}

.toppicks__carousel .swiper-pagination-bullet {
    background-color: #7e7e7e;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    margin-top: 2rem;
}

.toppicks__carousel {
    padding-bottom: 6rem;
}

.toppicks__carousel .swiper-pagination-bullet-active {
    background-color: var(--title-color);
}

.swiper-button-prev,
.swiper-button-next {
    color: var(--text-color);
}

.swiper-button-prev {
    left: 0;
    top: 11rem;
}

.swiper-button-next {
    right: 0;
    top: 11rem;
}

.toppicks__button {
    display: flex;
    align-items: center;
    justify-content: center;
}

.toppicks__button i {
    font-size: 1.5rem;
    border: 1px solid var(--accent-color);
    padding: 0.5rem;
    border-radius: 50%;
    color: var(--accent-color);
}

.toppicks__like {
    position: absolute;
    top: 0;
    right: 4rem;
    margin-top: 0.5rem;
    margin-right: 0.5rem;
    padding: 0rem 0.5rem;
    padding-top: 0.2rem;
    font-size: 1.5rem;
    color: var(--nav-overlay-color);
    background-color: #fff;
    border-radius: 1rem;
    cursor: pointer;
    transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
}

.toppicks__like:hover {
    color: rgb(255, 121, 121);
    background-color: #ffdbdb;
}