.picks {
    width: 100%;
    cursor: grab;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    gap: 0.5rem;
}

.picks::-webkit-scrollbar {
    display: none;
}




.picks__wrapper {
    white-space: nowrap;
    margin-top: 3rem;

}

.picks__inner__card img {
    width: 25rem;
    height: 12.5rem;
    object-fit: cover;
    border: 0.7px solid var(--text-color);
    margin-left: 1rem;
}

.picks__inner__card {
    display: inline-block;
    position: relative;
    width: 30vw;
    scroll-snap-align: start;
    transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
    margin-top: 5rem;
}


.picks__inner__card__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.picks__inner__card__info h1 {
    font-size: 1.3rem;
    font-weight: var(--font-bold);
    color: var(--title-color);
    text-transform: uppercase;
    font-family: "Satoshi-Bold", sans-serif;
    letter-spacing: 0.125rem;
    display: flex;
    text-align: left;
    justify-content: flex-start;
    gap: 0.5rem;
    width: 100%;
    padding-top: 0.5rem;
    margin-left: 1rem;
}